import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import { useState, Fragment, useEffect, useContext } from 'react';
import IconCancel from '@material-ui/icons/Cancel';
import ConversationSearch from '../views/messenger/ConversationSearch'
import NotificationContext from './Context'
import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { FixedSizeList } from 'react-window';
import { DialogContentText } from '@material-ui/core';
import { Loading } from 'react-admin';
import Checkbox from '@material-ui/core/Checkbox';
import LinkIcon from '@material-ui/icons/Link';
import { useDataProvider, Button } from 'react-admin';
import moment from 'moment';
import ViewHeadlineIcon from '@material-ui/icons/ViewHeadline';
import {useHistory} from "react-router-dom";
window.moment = moment
var dateFormat = require("dateformat");

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        height: 600,
        maxWidth: 450,
        backgroundColor: theme.palette.background.paper,
    },
}));




export default function LinkSquarePayment(props) {
    const history = useHistory();
    const dataProvider = useDataProvider();
    const classes = useStyles();
    const { linkSquareModal, openSquareLink, selectedPayments } = useContext(NotificationContext)
    const [todayBookings, setTodayBookings] = useState([])
    const [todayBookinsHolder, setTodaysBookingsHolder] = useState([])
    const [total, setTotal] = useState('')
    const [searchText, setSearchText] = useState('')
    const [loading, setLoading] = useState(true)
    const [checked, setChecked] = React.useState([]);
    const [viewToday, setViewToday] = React.useState(true)

    const handleToggle = (value) => () => {

        const currentIndex = checked.indexOf(value);
        const newChecked = [...checked];

        if (currentIndex === -1) {
            newChecked.push(value);
        } else {
            newChecked.splice(currentIndex, 1);
        }

        setChecked(newChecked);
    };

    useEffect(() => {

        async function fetchData() {
            setLoading(true)
            let today = new Date()
            let all_clients
            viewToday ? all_clients = await dataProvider.getList('bookings', { filter: { 'start': dateFormat(today, "yyyy-mm-dd") }, 'pagination': { "page": 1, "perPage": 10000 } }) : all_clients = await dataProvider.getList('bookings', { 'pagination': { "page": 1, "perPage": 10000 } })
            all_clients.data.forEach(i => { i.date = i.start.substring(0, 10); })
            setTotal(all_clients.total)
            setTodayBookings(all_clients.data)
            setTodaysBookingsHolder(all_clients.data)
            setLoading(false)
        }

        fetchData()
    }, [viewToday])
    const searchFilterFunction = (text) => {

        const newData = todayBookinsHolder.filter(item => {


            let name
            try {
                name = item.name.toUpperCase()
            }
            catch { name = null }
            let dogName
            try {
                dogName = item.dogName.toUpperCase()
            }
            catch { name = null }

            let date
            try {
                date = item.date.toUpperCase()
            }
            catch { date = null }

            const itemData = `${name} ${dogName} ${date}`;
            const textData = text.toUpperCase();

            return itemData.indexOf(textData) > -1;
        });

        setSearchText(text)
        setTodayBookings(newData)
        setTotal(newData.length)

    };

    const handleClick = async () => {
        let paymentsIDS = selectedPayments
        let bookingIDS = checked.map(i=>i.id)
        await dataProvider.updateMany('payments',{ids:paymentsIDS, data:{booking_ref: bookingIDS, linked:true}})
        await dataProvider.updateMany('bookings', {ids: bookingIDS, data:{payment_ref: paymentsIDS}})
        setChecked([])
        props.refetch()
        openSquareLink()
    }



    function renderRow(props) {
        const { index, style } = props;

        return (
            <ListItem button style={style} key={index} >
                <ListItemText primary={`${todayBookings[index].name} - ${todayBookings[index].dogName}`} secondary={`${todayBookings[index].service} - ${todayBookings[index].date}`} />
                <Checkbox
                    edge="end"
                    onChange={handleToggle(todayBookings[index])}
                    checked={checked.indexOf(todayBookings[index]) !== -1}

                />
            </ListItem>
        );
    }

    renderRow.propTypes = {
        index: PropTypes.number.isRequired,
        style: PropTypes.object.isRequired,
    };

    return (

        loading ? <Loading /> : (<Fragment>

            <Dialog open={linkSquareModal}>

                <DialogTitle>Link Square payment with booking</DialogTitle>

                <DialogContent>
                    <DialogContentText>Search "Client Dog YYYY-MM-DD"</DialogContentText>
                    <div className={classes.root}>
                        <ConversationSearch searchText={searchText} searchFilterFunction={searchFilterFunction} />
                        <FixedSizeList height={550} width={450} itemSize={60} itemCount={total} >
                            {renderRow}
                        </FixedSizeList>
                    </div>

                </DialogContent>

                <DialogActions>
                    <Button
                        label={viewToday ? 'Today' : 'All'}
                        onClick={() => setViewToday(!viewToday)}
                    >
                        <ViewHeadlineIcon />
                    </Button>
                    {checked.length !== 0 && <Button
                        label="Link"
                     onClick={()=>handleClick()}
                    >
                        <LinkIcon />
                    </Button>}

                    <Button
                        label="Cancel"
                        onClick={() => openSquareLink()}
                    >
                        <IconCancel />
                    </Button>

                </DialogActions>

            </Dialog>
        </Fragment>)

    )
}

