import React from 'react';
import PaymentsView from './PaymentsView'
import { useQueryWithStore, Loading, Error } from 'react-admin';



export default function UnlinkedPayments() {




    const { loaded, error, data, refetch } = useQueryWithStore({
        type: 'getList',
        resource: 'payments',
        payload: { "filter": { 'system': 'Square' , 'linked': false}, 'pagination': { "page": 1, "perPage": 1000000 }, }
    });

    if (!loaded) { return <Loading />; }
    if (error) { return <Error />; }


    return <PaymentsView rows={data} title='Unlinked Payments' refetch={refetch} />

}










