import * as React from "react";
import {

    EditButton,
    useDataProvider, Loading, Error, useQueryWithStore

} from 'react-admin';
import { Title, Button, changeLocale } from 'react-admin';
import MyUrlField from '../fields/MyUrlField';
import { useMediaQuery } from '@material-ui/core';
import CustomizableDatagrid from 'ra-customizable-datagrid';
import ReactDataGrid from '@inovua/reactdatagrid-community';
import '@inovua/reactdatagrid-community/index.css';
import NumberFilter from '@inovua/reactdatagrid-community/NumberFilter'
import DateFilter from '@inovua/reactdatagrid-community/DateFilter'
import SelectFilter from '@inovua/reactdatagrid-community/SelectFilter'
import StringFilter from "@inovua/reactdatagrid-community/StringFilter"
import CheckBox from '@inovua/reactdatagrid-community/packages/CheckBox'
import BoolFilter from '@inovua/reactdatagrid-community/BoolFilter'
import SendIcon from '@material-ui/icons/Send';
import MonetizationOnIcon from '@material-ui/icons/MonetizationOn';
import { NotificationManager } from 'react-notifications';
import moment from 'moment';
import Chip from '@material-ui/core/Chip'
import FilterListIcon from '@material-ui/icons/FilterList';
import FavoriteIcon from '@material-ui/icons/Favorite';
import DoneIcon from '@material-ui/icons/Done';
import AttachmentsShow from '../components/AttachmentsShow'
import { ifAttchaments } from '../components/authProvider'
import BoolEditor from "@inovua/reactdatagrid-community/BoolEditor";
import NotificationContext from '../components/Context'
import LinkSquarePayment from "../components/linkSquarePayment";
import LinkIcon from '@material-ui/icons/Link';
import { DragHandle } from "@material-ui/icons";
window.moment = moment
var dateFormat = require("dateformat");



export default function DogView(props) {

    const dataProvider = useDataProvider();
    const [selected, setSelected] = React.useState({})
    const [filter, setFilter] = React.useState(false)
    const [rows, setRows] = React.useState([])
    const [loading, setLoading] = React.useState(true);
    const [error, setError] = React.useState();
    const { linkSquareModal, openSquareLink, setPayments } = React.useContext(NotificationContext)

    const handleLinkButton = ()=>{
        let selectedPayme = Object.values(selected).map(i=>i.id)
        setPayments(selectedPayme)
        openSquareLink()
        
    }

    const onEditComplete = async ({ value, columnId, rowIndex, data }) => {
        try {
            if (columnId === 'deposit') {


                let nrows = rows
                nrows[nrows.findIndex(i => i.id === data.id)].deposit = value
                setRows(nrows)
                await dataProvider.update('payments', { id: data.id, data: { deposit: value } })
            }

        }
        catch { }

    }



    const columns = [
        {
            name: 'description', header: 'Description', defaultWidth: 200, editable: false
        },
        {
            name: 'status', header: 'Status', defaultWidth: 130, filterEditor: SelectFilter, editable: false,
            filterEditorProps: {
                multiple: true,
                wrapMultiple: false,
                dataSource: ["succeeded", 'failed'].map(c => {
                    return { id: c, label: c.charAt(0).toUpperCase() + c.slice(1) }
                }),
            }, render: ({ value }) => {
                if (value === 'succeeded') {
                    return <Chip color='primary' style={{ backgroundColor: 'green' }} label={'Suceeded'} key={value} />
                }
                else if (value === 'failed') {
                    return <Chip color='primary' style={{ backgroundColor: 'red' }} label={'Failed'} key={value} />
                }
            }
        },
        {
            name: 'name', editable: false, header: 'Name', defaultWidth: 200, render: ({ value, data }) => {
                try {

                    return <a href={'#clients/' + data.bookingData[0].client_ref} ><span style={{ paddingRight: '5px' }}>{value}</span></a>



                }
                catch { return value }


            }
        },
        {
            name: 'bookingData', editable: false, header: 'Bookings', defaultWidth: 200, render: ({ value, data }) => {
                try {
                    return <>
                        {value.map(i => {

                            return (<a href={'#bookings/' + i.id} ><span style={{ paddingRight: '5px' }}>{i.code}</span></a>)

                        })}
                    </>
                }
                catch { return }


            }
        },
        {
            name: 'dollaramount', editable: false, header: 'Amount', defaultWidth: 90, filterEditor: NumberFilter, render: ({ value, data }) => { return `$${value}` }
        },
        {
            name: 'deposit', header: 'Deposit', filterEditor: BoolFilter, defaultWidth: 90, render: ({ value }) => value ? < DoneIcon htmlColor='green' style={{ textAlign: 'center', verticalAlign: 'center' }} /> : null, editor: BoolEditor
        },
        {
            name: 'email', editable: false, header: 'Email', defaultWidth: 200
        },
        {
            name: 'system', editable: false, header: 'System', defaultWidth: 130, render: ({ value }) => {
                if (value === 'Stripe') {
                    return <Chip color='primary' style={{ backgroundColor: 'blue' }} label={'Stripe'} key={value} />
                }
               else if (value === 'Square') {
                    return <Chip color='primary' style={{ backgroundColor: 'green' }} label={'Square'} key={value} />
                }

            }, filterEditor: SelectFilter, filterEditorProps: {
                multiple: true,
                wrapMultiple: false,
                dataSource: ["Stripe", "Square"].map(c => {
                    return { id: c, label: c.charAt(0).toUpperCase() + c.slice(1) }
                })
            }
        },





    ];

    const gridStyle = { minHeight: 550 };


    const defaultFilterValue = [


        { name: 'status', operator: 'inlist', type: 'select', value: null },
        { name: 'description', operator: 'contains', type: 'string', value: '' },
        { name: 'name', operator: 'contains', type: 'string', value: '' },
        { name: 'email', operator: 'contains', type: 'string', value: '' },
        { name: 'amount', operator: 'gte', type: 'number', value: '' },
        { name: 'system', operator: 'inlist', type: 'select', value: null },

        { name: 'deposit', operator: 'eq', type: 'boolean', value: null },

    ];




    React.useEffect(() => {

        async function fetchData() {
            let data = props.rows || []
            let BookinIds = []
            data.forEach(i => {
                i.booking_ref && i.booking_ref.forEach(y => BookinIds.push(y))
                let amount = i.amount/100
                i.dollaramount = amount
            })

            let bookingData = await dataProvider.getMany('bookings', { ids: BookinIds, 'pagination': { "page": 1, "perPage": 1000000 } })


            data.forEach(payment => {
                if (payment.booking_ref) {
                    let bookingsData = []
                    payment.booking_ref.forEach(i => {
                        let booking = bookingData.data.filter(y => y.id === i)
                        bookingsData.push(booking[0])
                    })
                    payment.bookingData = bookingsData


                }
            })
            setRows(data);
            setLoading(false);

        }
        fetchData()

    }, [props.rows])





    if (loading) return <Loading />;
    if (error) return <Error />;






    return (
        <>
        {linkSquareModal && <LinkSquarePayment refetch={props.refetch} /> }
            <Title title={props.title} />
            <div>
                <Button
                    label="Filters"
                    onClick={() => setFilter(!filter)}>
                    <FilterListIcon />
                </Button>
                {Object.keys(selected).length === 1 && <EditButton
                    basePath='/dogs' label='Edit' record={Object.values(selected)[0]}
                />}
                {Object.keys(selected).length !== 0 && <Button
                    label="Message Client">
                    <SendIcon />
                </Button>}
                {Object.keys(selected).length !== 0 && props.title==='Unlinked Payments' && <Button
                    onClick={() => handleLinkButton()}
                    label="Link Payment">
                    <LinkIcon />
                </Button>}
            </div>
            <ReactDataGrid
                idProperty="id"
                columns={columns}
                dataSource={rows}
                style={gridStyle}
                defaultFilterValue={defaultFilterValue}
                onSelectionChange={select => setSelected(select.selected)}
                enableFiltering={filter}
                onEditComplete={onEditComplete}
                editable={true}
                multiSelect
                checkboxColumn
            />
        </>
    )
}




