export const getAttchaments = async (dogId, full) => {


  var storage = firebase.storage();
  var links = []
  var storageRef = storage.ref();
  try {
    var listRef = storageRef.child(`attachments/${dogId}`);
    var list = await listRef.listAll()
    for (let i of list.items) {
      let url = await i.getDownloadURL()
      links.push(url)
    }
  }
  catch {

    return false
  }

  return links

}

export const ifAttchaments = async () => {


  var storage = firebase.storage();

  var storageRef = storage.ref();

  var listRef = storageRef.child(`attachments`);
  var list = await listRef.listAll()
  list = list.prefixes.map(i => { return i.location.path_.split('/')[1] })

  return list



}

export const UploadAttach = async (dogId, len, file) => {

  var storage = firebase.storage();
  var storageRef = storage.ref();
  let Len = len
  try {

    for (let i of file) {
      let listRef = storageRef.child(`attachments/${dogId}/file${Len}.jpg`);
      Len++
      await listRef.put(i)
    }


  }
  catch (e) {


  }


}



